	module.exports = {"data":{"challengeNode":{"title":"Set the id of an Element","description":["除了class属性，每一个 HTML 元素也都有<code>id</code>属性。","使用<code>id</code>有几个好处：你可以通过<code>id</code>选择器来改变单个元素的样式，稍后的课程中，你也会了解到在 JavaScript 里面，可以通过<code>id</code>来选择元素和操作元素。","<code>id</code>属性应是唯一的。浏览器不强迫执行这规范，但是这是广泛认可的最佳实践。所以，请不要给多个元素设置相同的<code>id</code>属性。","设置<code>h2</code>元素的 id 为<code>cat-photo-app</code>的方法如下：","<code>&#60;h2 id=\"cat-photo-app\"></code>","<hr>","设置<code>form</code>元素的 id 为<code>cat-photo-form</code>。"],"challengeType":0,"fields":{"slug":"/responsive-web-design/basic-css/set-the-id-of-an-element","blockName":"Basic CSS","tests":[{"text":"你的<code>form</code>元素的 id 应为<code>cat-photo-form</code>。","testString":"assert($(\"form\").attr(\"id\") === \"cat-photo-form\", '你的<code>form</code>元素的 id 应为<code>cat-photo-form</code>。');"}]},"required":[],"files":{"indexhtml":{"key":"indexhtml","ext":"html","name":"index","contents":"<link href=\"https://fonts.googleapis.com/css?family=Lobster\" rel=\"stylesheet\" type=\"text/css\">\n<style>\n  .red-text {\n    color: red;\n  }\n\n  h2 {\n    font-family: Lobster, monospace;\n  }\n\n  p {\n    font-size: 16px;\n    font-family: monospace;\n  }\n\n  .thick-green-border {\n    border-color: green;\n    border-width: 10px;\n    border-style: solid;\n    border-radius: 50%;\n  }\n\n  .smaller-image {\n    width: 100px;\n  }\n\n  .silver-background {\n    background-color: silver;\n  }\n</style>\n\n<h2 class=\"red-text\">CatPhotoApp</h2>\n<main>\n  \n  <a href=\"#\"><img class=\"smaller-image thick-green-border\" src=\"http://cdn.freecodecamp.cn/relaxing-cat.jpg\" alt=\"一只仰卧着的萌猫\"></a>\n  \n  <div class=\"silver-background\">\n    <p>猫咪最喜欢的三件东西：</p>\n    <ul>\n      <li>猫薄荷</li>\n      <li>激光笔</li>\n      <li>千层饼</li>\n    </ul>\n    <p>猫咪最讨厌的三件东西：</p>\n    <ol>\n      <li>跳蚤</li>\n      <li>打雷</li>\n      <li>同类</li>\n    </ol>\n  </div>\n  \n  <form action=\"/submit-cat-photo\">\n    <label><input type=\"radio\" name=\"indoor-outdoor\">室内</label>\n    <label><input type=\"radio\" name=\"indoor-outdoor\">室外</label><br>\n    <label><input type=\"checkbox\" name=\"personality\">忠诚</label>\n    <label><input type=\"checkbox\" name=\"personality\">懒惰</label>\n    <label><input type=\"checkbox\" name=\"personality\">积极</label><br>\n    <input type=\"text\" placeholder=\"猫咪图片地址\" required>\n    <button type=\"submit\">提交</button>\n  </form>\n</main>","head":"","tail":""},"indexjs":null,"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[],"nextChallengePath":"/responsive-web-design/basic-css/use-an-id-attribute-to-style-an-element","id":"bad87eee1348bd9aede07836"},"slug":"/responsive-web-design/basic-css/set-the-id-of-an-element"}}

